<template>
  <div class="comment">
    <div v-for="(item,index) in comments" :key="index" class="content">
      <div class="header">
        <div class="userInfo">
          <img src="../../assets/me_icon/yaodian.png" />
          <div class="userName">
            <p>{{ item.compName }}</p>
            <p>{{ item.createtdate }} {{ item.createtime }}</p>
          </div>
        </div>
        <div class="score">
          <span>综合评分</span>
          <van-rate v-model="item.level" readonly :size="comment" />
        </div>
      </div>
      <div class="comments">
        {{ item.content }}
      </div>
    </div>
    <div class="bgImg" v-if="comments.length < 1">
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: 3,
      goods: this.$router.params.goods,
      comments: [],
      comment: 15
    }
  },
  mounted() {
    this.getComments()
  },
  methods: {
    getComments() {
      JNB.query({
        serverName: "goodsServer",
        cls: "ProdModule",
        method: "getGoodsApprise",
        params: {
          sku: this.goods.sku
        },
        callback: data => {
          let result = JSON.parse(data);

          if (result.code === 200) {
            this.comments = result.data
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    }
  }
}
</script>
<style scoped>
*{
  margin: 0;
  padding: 0;
}
.comment{
  padding-top: 30px;
}
.comment .content{
  width: 100%;
  border-bottom: 1px solid rgb(238, 236, 233);
  background:rgba(255,255,255,1);
}
.comment .content .header{
  width: 100%;
  height: 100px;
}
.comment .content .header img{
  width: 60px;
  height: 60px;
  padding: 20px 20px 0;
  float: left;
}
.comment .content .header .userName{
  float: left;
  width: 350px;
  padding: 15px 15px 0 0;
  color:rgba(51,51,51,1);
}
.comment .content .header .userName p:first-child{
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 10px;
}
.comment .content .header .userName p:last-child{
  font-size: 20px;
  font-weight: 500;
  color: rgba(153,153,153,1);
}
.comment .content .header .score{
  float: right;
  width: 280px;
  padding: 20px 0;
}
.comment .content .header .score span{
  float: left;
  font-size:24px;
  font-weight:500;
  margin-top: 2px;
  margin-right: 10px;;
  color:rgba(153,153,153,1);
}
.comment .content .comments{
  width: 100%;
  padding: 10px 20px 20px 100px;
  box-sizing: border-box;
}
.comment .bgImg{
  width: 100%;
  height: 70vh;
  background: url('~@/assets/noneImg/comment.png') no-repeat;
  background-size: 50%;
  background-position: center center;
}
</style>
